.water {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 20px;
  overflow: hidden;
}
.water .box {
  color: #000;
  z-index: 2;
  position: absolute;
  left: 0;
  top: 0;
  word-break: break-all;
  padding: 5px;
}
.water:before,
.water:after {
  content: "";
  position: absolute;
  width: 400px;
  height: 400px;
  top: 0;
  left: 50%;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 45%;
  -webkit-transform: translate(-50%, -80%) rotate(0);
  transform: translate(-50%, -80%) rotate(0);
  -webkit-animation: rotate 6s linear infinite;
  animation: rotate 6s linear infinite;
}
.water:after {
  border-radius: 47%;
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-transform: translate(-50%, -80%) rotate(0);
  transform: translate(-50%, -80%) rotate(0);
  -webkit-animation: rotate 10s linear -5s infinite;
  animation: rotate 10s linear -5s infinite;
}
@-webkit-keyframes rotate {
  50% {
    -webkit-transform: translate(-50%, -80%) rotate(180deg);
    transform: translate(-50%, -80%) rotate(180deg);
  }
  100% {
    -webkit-transform: translate(-50%, -80%) rotate(360deg);
    transform: translate(-50%, -80%) rotate(360deg);
  }
}
@keyframes rotate {
  50% {
    -webkit-transform: translate(-50%, -80%) rotate(180deg);
    transform: translate(-50%, -80%) rotate(180deg);
  }
  100% {
    -webkit-transform: translate(-50%, -80%) rotate(360deg);
    transform: translate(-50%, -80%) rotate(360deg);
  }
}
